import React from "react";
import { Button } from "react-bootstrap";

var array = [];
var direction = [];
var colors = [];
var userarray = [];

var edge;


export default function RedBlackTreeGyak() {
  function generate() {
    var hossz = Math.floor(Math.random() * 3) + 10;

    for (let i = 0; i < hossz; i++) {
      array.push(Math.floor(Math.random() * 100));
    }

    console.log("hossz: " + hossz);
    console.log("array: " + array);
    document.getElementById("generate").innerHTML =
      "Oldja meg a piros-fekete fa hasznltval a feladatot, a kvetkez adatsorra: " +
      "[ " +
      array +
      " ].";
  }

  function Balrahalad() {
    if (direction.length < 4) {
      direction.push("balra");
      console.log(direction);
      document.getElementById("position").innerHTML = " " + direction + " ";
    }
  }

  function Jobbrahalad() {
    if (direction.length < 4) {
      direction.push("jobbra");
      console.log(direction);
      document.getElementById("position").innerHTML = " " + direction + " ";
    }
  }

  function Visszavon() {
    if (direction.length != 0) {
      direction.pop();
      console.log(direction);
      document.getElementById("position").innerHTML = " " + direction + " ";
    }
  }

  function Kirajzol() {
    console.log("new");
    var element = document.getElementById("element").value;
    var color;
    if (document.getElementById("check-red").checked) {
      color = "red";
      colors.push(color);
    } else if (document.getElementById("check-black").checked) {
      color = "black";
      colors.push(color);
    }



    const divelement = document.createElement("b");

    if (color == "red") {
      divelement.classList.add("tree-red");
    } else if (color == "black") {
      divelement.classList.add("tree-black");
    }

    let deepcounter = direction.length;

    for (let i = 0; i < 17; i++) {
        userarray[i]="NIL";
    }

    for (let i = 0; i < direction.length; i++) {
      if (direction.length != 0) {
        if (direction[i] == "balra") {
            userarray[userarray.length-1]=element;
            colors[userarray.length-1]=color;
        } else if (direction[i] == "jobbra") {
            userarray[userarray.length-1]=element;
            colors[userarray.length-1]=color;
        }
      }else{
        userarray[0]=element;
        colors[0]=color;
      }
    }

    divelement.textContent = element;
    divelement.id = "element" + array.length;
    const final = document.getElementById("final");
    final.appendChild(divelement);

    console.log(color);
    console.log("Colors:" + colors);
    console.log(element);
    console.log("Elements:" + userarray);

    document.getElementById("element").value = "";
    document.getElementById("check-black").checked = false;
    document.getElementById("check-red").checked = false;
    direction = [];
  }

  return (
    <div>
      <div className="test">
        <h2>Mester ttel feladat genertor</h2>
        <Button variant="warning" onClick={generate}>
          j feladat
        </Button>
        <br />
        <br />
        <p id="leiras" />
        <div className="normalpad" id="generate"></div>
        <br />
        <div>
          <b>rtk: </b>
          <input type="number" id="element"></input>
          <div class="">
            <input type="checkbox" id="check-black" />
            <label>Black</label>
            <input type="checkbox" id="check-red" />
            <label>Red</label>
          </div>
          <br />
          <Button variant="success" onClick={Balrahalad}>
            Balra Megy
          </Button>
          <Button variant="success" onClick={Jobbrahalad}>
            Jobbra Megy
          </Button>
          <Button variant="success" onClick={Visszavon}>
            Visszavon
          </Button>
          <br />
          <Button variant="success" onClick={Kirajzol}>
            Kirajzol
          </Button>
          <br />
          <br />
          <div className="highpad">
            <b>Aktulis pozci: </b>
            <b id="position" />
          </div>
          <div className="highpad">
            <b id="final"></b>
          </div>
          <br />
          <Button variant="warning">Ellenrzs</Button>
        </div>
      </div>
    </div>
  );
}
